.titleField
{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
}

.title-wrapper
{
  width: 60%;
  display:flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.title-wrapper img
{
  width: 80px;
}

@media screen and (min-width: 768px)
{
  .title-wrapper img
  {
  width: 100px;
  }
}

.title
{
  width: 50%;
  font-family: var(--title-font);
  font-size: 3.9rem;
  letter-spacing: .2rem;
  text-align: left;
}

.titleFieldLeft
{
  display:flex;
  width: 35%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

.darkThemeToggle
{
  margin-bottom: 1em;
}

.date
{
  font-weight: bold;
  font-family: var(--body-font);
  font-size: 1em;
  color: var(--light-grey);
}

.searchContainer
{
  display:flex;
  flex-direction: row;
  border-radius: var(--border-radius-basic);
  box-shadow: var(--box-shadow);

  position: relative;
}

.searchField
{
  width: 100%;
  
  padding: var(--field-basic-padding);
  border-style: none;
  border-radius: var(--border-radius-basic);

  text-align: center;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

.searchField:focus
{
  outline: 2px solid rgba(255, 197, 8, 0.507);
}

.icon
{
  position:absolute;
  top: 1.2rem;
  left: 1rem;
  color: var(--light-grey);
}

@media screen and (min-width: 768px)
{
  .icon
  {
    top: 1rem;
  }
}

@media screen and (min-height: 950px)
{
  .icon
  {
    top: 1.6rem;
  }
}
.TodoCounter-field
{
  display:flex;
  justify-content: space-between;
  align-items: center;
  
  border-color: var(--background-color);
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius-basic);
  padding: var(--basic-padding);
  
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}



.TodoList-wrapper
{
  display:grid;
  grid-template-columns: 1fr;
  grid-auto-rows: minmax(-webkit-min-content, -webkit-max-content);
  grid-auto-rows: minmax(min-content, max-content);
  grid-row-gap: 5%;
  row-gap: 5%;
  overflow-y:auto;
}


.TodoItem
{
  width: 95%;
  min-height: 2.5em;

  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin: 0 auto;
  border-radius: var(--border-radius-field);
  box-shadow: var(--box-shadow-item);
  background-color: var(--background-field);
  
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

.TodoItem:first-child
{
  margin-top: 2%;
}

.TodoItem:last-child
{
  margin-bottom: 4%;
}

.ItemText
{
  width: 100%;
  padding: 4px 8px;
}

.ItemText-complete
{
  text-decoration: line-through;
}

.check-icon-wrapper
{
  width: 3rem;
  color:var(--light-grey);
  margin-left: .5em;
}

.check-icon-wrapper:hover
{
  cursor:pointer;
  color: var(--green);
}

.check-icon-active
{
  color:var(--green);
}

.close-icon-wrapper
{
  width: 3rem;
  color:var(--light-grey);
  margin-right: .5em;
}

.close-icon-wrapper:hover
{
  color: var(--red);
  cursor: pointer;
}

.CreateTodoButton-wrapper
{
  display:flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.clear-button
{
  margin-top: .3em;
  width: 2.8rem;
  color: var(--red);
}

.add-button
{
  width: 3.8rem;
  color: var(--green);
}

.clear-button:hover,
.add-button:hover
{
  cursor: pointer;
}

.clear-button:active,
.add-button:active
{
  -webkit-transform: scaleX(.95) scaleY(.95);
          transform: scaleX(.95) scaleY(.95);
}
.state-content
{
  display: grid;
  place-items: center;
  height: 50vh;
}

.svg-img
{
  width: 250px;
}
.main {
  stroke-dasharray: 50;
  stroke-dashoffset: 0;
  -webkit-animation: load 5s linear infinite;
          animation: load 5s linear infinite;
  -webkit-transform-origin: center;
          transform-origin: center;
}

@-webkit-keyframes load {
  0% {
    stroke-dashoffset: 50;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -50;
  }
}

@keyframes load {
  0% {
    stroke-dashoffset: 50;
  }
  50% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: -50;
  }
}

.state-content
{
  display: grid;
  place-items: center;
  height: 50vh;
}

.state-loading
{
  height: 30vh;
}
.modal-background
{
  background-color: rgba(32, 27, 15, 0.877);
  z-index: 10;
  position: fixed;
  top: -10px;
  bottom: -10px;
  right: -10px;
  left: -10px;
  display:flex;
  justify-content: center;
  align-items: center;
}

.modal-wrapper
{
  width: 60%;
  display:grid;
  place-items: center;
}

@media screen and (min-width: 768px)
{
  .modal-wrapper
  {
    width: 40%;
    height: 20%;
  }
}

.textarea-add-task
{
  width:100%;
  height: 100%;
  text-align: center;
  padding: 12px;
  border-style: none;
  border-radius: var(--border-radius-basic);
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  margin: 1em;
  position: relative;
  display:block;
  resize: none;
}

.textarea-add-task:focus
{
  outline: 2px solid var(--light-grey);
}

.buttons-wrapper
{
  display:flex;
  justify-content: center;
  width: 50%;
}

.modal-close
{
  display:flex;
  justify-self: flex-end;
  color:var(--red);
  width: 30px;
  margin-bottom: 1em;
}

.add-modal
{
  margin-top: 1em;
}

@media screen and (min-width: 1024px)
{
  .modal-wrapper
  {
    width:40%;
  }
}

@media screen and (min-width: 1366px)
{
  .modal-wrapper
  {
    width:25%;
  }
}

:root
{
  --background: #ffffff;
  --background-field: #d8d8d8bd;
  --red: #b81d1d;
  --green: #1D9A57;
  --black: #08080C;
  --white: #ffffff;
  --light-grey: lightslategray;
  --grey-dark: rgba(16, 14, 36, 0.486);
  --grey-dark-plus : rgba(14, 14, 14, 0.900);
  --transparent: transparent;
  --font-color: #333;
  --body-color: rgba(114, 114, 113, 0.884);
  --background-color: #eee;

  /* Toggle sizes */
  --height-toggle: 28px;
  --height-slider: 24px;
  --width-toggle: 44px;
  --slider-toggle: 18px;
}

[data-theme='dark']
{
  --font-color: #eee;
  --background-color:rgba(14, 14, 14, 0.76);
  --background-field:rgba(75, 74, 74, 0.657);
  --box-shadow: 0 none !important;
  --box-shadow-item: 0 none !important;
}

body
{
  color: #333;
  color: var(--font-color);
  background-color: rgba(114, 114, 113, 0.884);
  background-color: var(--body-color);
}

/* Toggle */

.toggle-theme-wrapper
{
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 4px;
  gap: 4px;
  color: #333;
  color: var(--font-color);
}

.toggle-theme 
{
  position: relative;
  display: inline-block;
  height: 28px;
  height: var(--height-toggle);
  width: 44px;
  width: var(--width-toggle);
}

.toggle-theme input 
{
  display: none;
}

.slider 
{
  background-color: #ccc;
  position: absolute;
  cursor: pointer;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  transition: 0.2s;
}

.slider:before 
{
  background-color: #fff;
  bottom: 4px;
  content: "";
  height: 24px;
  height: var(--height-slider);
  height: 80%;
  left: 4px;
  position: absolute;
  transition: 0.4s;
  width: 18px;
  width: var(--slider-toggle);
}

input:checked + .slider:before 
{
  -webkit-transform: translateX(18px);
          transform: translateX(18px);
  -webkit-transform: translateX(var(--slider-toggle));
          transform: translateX(var(--slider-toggle));
}

input:checked + .slider 
{
  background-color: cornflowerblue;
}

.slider.round 
{
  border-radius: 32px;
}

.slider.round:before
{
  border-radius: 50%;
}

/* SVG of toggle */
.svg-title
{
  width: 30px;
}
:root
{
  /* Fonts */
  --title-font: 'Squada One', cursive;
  --body-font: 'League Spartan', sans-serif;

  /* Fonts sizes */

  --tiny-font-size: 1rem;
  --small-font-size: 1.4rem;
  --normal-font-size: 1.6rem;
  --medium-font-size: 2rem;
  --big-font-size: 2.4rem;
  --title-font-size: 3.2rem;

  /* Paddigs */
  --basic-padding: .4em .8em; 

  /* Spaces */
  --field-basic-padding: .4em .8em;
  --margin-space-sections: 2%;

  /* Borders */
  --border-radius-basic: 10px;
  --border-radius-field: 5px;

  /* SHADOWS */
  --box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
              rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  --box-shadow-item: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  --box-shadow-button: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

*,
*::after,
*::before
{
  padding: 0;
  margin:0;
  box-sizing: border-box;
}

html
{
  height: 100vh;
  font-size: 62.5%;
}

.body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root
{
  min-height: 100vh;
  max-width: 450px;
  display:grid;
  grid-template-columns: 1fr;
  grid-template-rows: 12% 4% 5% 60% 5%;
  grid-row-gap: 3%;
  row-gap: 3%;

  margin: 0 auto;
  padding: 1em;

  border-radius: 10px;
  background-color: var(--background-color);
}

@media screen and (min-width: 768px)
{
  #root
  {
    min-height: 95vh;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
              rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    box-shadow: var(--box-shadow);
    margin-top: 2vh;
    padding: 1em 2em;
  }
}

.default-text
{
  width: 90%;
  margin: 0 auto;

  font-weight: bold;
  font-family: 'League Spartan', sans-serif;
  font-family: var(--body-font);
  font-size: 1.4rem;
  font-size: var(--small-font-size);
  color: var(--light-grey);
  line-height: 1.2em;
  text-align: center;
}
